                    justifyContent: 'flex-start',
                    textAlign: 'left',
                    textTransform: 'none',
                    borderRadius: 2,
                    py: 1.5,
                    px: 2,
                    borderColor: '#D1D5DB',
                    color: '#374151',
                    backgroundColor: 'white',
                    '&:hover': {
                      backgroundColor: '#F9FAFB',
                      borderColor: '#9CA3AF',
                    }
