import React, { useState, useMemo } from 'react';
import {
  Box,
  Container,
  Typography,
  Alert,
} from '@mui/material';
import Header from '../components/Header';
import WelcomeBanner from '../components/WelcomeBanner';
import FilterControls from '../components/FilterControls';
import TodaysSignals from '../components/TodaysSignals';
import DashboardTile from '../components/DashboardTile';
import { dashboardTiles } from '../utils/mockData';
import { FilterOption, SortOption } from '../types';

interface HomepageProps {
  user: {
    name: string;
    title: string;
  };
  onLogout: () => void;
  onNavigateToProviderProforma: () => void;
}

const Homepage: React.FC<HomepageProps> = ({ user, onLogout, onNavigateToProviderProforma }) => {
  const [activeFilter, setActiveFilter] = useState<FilterOption>('all');
  const [activeSort, setActiveSort] = useState<SortOption>('featured');
  const [currentView, setCurrentView] = useState<'grid' | 'list'>('grid');

  const filteredAndSortedTiles = useMemo(() => {
    let filtered = dashboardTiles;

    // Apply category filter
    if (activeFilter !== 'all') {
      filtered = filtered.filter(tile => tile.category === activeFilter);
    }

    // Apply sorting
    switch (activeSort) {
      case 'featured':
        filtered = [...filtered].sort((a, b) => {
          if (a.isFeatured && !b.isFeatured) return -1;
          if (!a.isFeatured && b.isFeatured) return 1;
          return b.usageCount - a.usageCount;
        });
        break;
      case 'alphabetical':
        filtered = [...filtered].sort((a, b) => a.title.localeCompare(b.title));
        break;
      case 'recent':
        filtered = [...filtered].sort((a, b) => {
          if (!a.lastAccessed && !b.lastAccessed) return 0;
          if (!a.lastAccessed) return 1;
          if (!b.lastAccessed) return -1;
          return b.lastAccessed.getTime() - a.lastAccessed.getTime();
        });
        break;
      case 'usage':
        filtered = [...filtered].sort((a, b) => b.usageCount - a.usageCount);
        break;
    }

    return filtered;
  }, [activeFilter, activeSort]);

  const handleTileClick = (tileId: string) => {
    console.log('Tile clicked:', tileId);
    
    // Update usage count (simulate user interaction)
    const tile = dashboardTiles.find(t => t.id === tileId);
    if (tile) {
      tile.usageCount += 1;
      tile.lastAccessed = new Date();
    }
    
    // Navigate to specific dashboards
    if (tileId === 'payment-integrity') {
      console.log('Navigating to Provider Proforma...');
      onNavigateToProviderProforma();
    } else {
      // In a real app, this would navigate to other specific dashboards
      console.log(`Navigating to dashboard: ${tileId}`);
      alert(`This would navigate to the ${tileId} dashboard`);
    }
  };

  const handleRefresh = () => {
    console.log('Refreshing dashboard data...');
    // In a real app, this would refresh data from the API
  };

  const handleViewChange = (view: 'grid' | 'list') => {
    setCurrentView(view);
  };

  return (
    <Box sx={{ minHeight: '100vh', backgroundColor: 'background.default', pb: { xs: '80px', sm: 0 } }}>
      <Header user={user} onLogout={onLogout} />
      
          <WelcomeBanner 
            user={user}
            onRefresh={handleRefresh}
            onViewChange={handleViewChange}
          />
          
          <FilterControls
            activeFilter={activeFilter}
            activeSort={activeSort}
            onFilterChange={setActiveFilter}
            onSortChange={setActiveSort}
            onViewChange={handleViewChange}
            currentView={currentView}
          />
          
          <TodaysSignals />
          
          <Container maxWidth="xl" sx={{ py: 4, pb: { xs: 6, sm: 4 } }}>
        {filteredAndSortedTiles.length === 0 ? (
          <Alert severity="info" sx={{ mb: 3 }}>
            <Typography variant="h6" gutterBottom>
              No dashboards found
            </Typography>
            <Typography>
              Try adjusting your filters or search criteria.
            </Typography>
          </Alert>
        ) : (
          <Box
            sx={{
              display: currentView === 'grid' ? 'grid' : 'flex',
              flexDirection: currentView === 'list' ? 'column' : 'unset',
              gridTemplateColumns: currentView === 'grid' ? {
                xs: '1fr',
                sm: 'repeat(2, 1fr)',
                md: 'repeat(3, 1fr)',
                lg: 'repeat(4, 1fr)',
              } : 'unset',
              gap: 3,
              mb: { xs: 8, sm: 0 },
            }}
          >
            {filteredAndSortedTiles.map((tile) => (
              <DashboardTile
                key={tile.id}
                tile={tile}
                onClick={handleTileClick}
                viewMode={currentView}
              />
            ))}
          </Box>
        )}
      </Container>
    </Box>
  );
};

export default Homepage;